//引入外部组件
import header from '@/components/header';
import button from '@/components/button';
export default {
  data() {
    return{
      titleContent: '我的宝宝',
      relationTitle: '请选择与学生关系',
      wayContent: '请选择绑定方式',
      relationFlag: false,
      bindWayFlag: false,
      relationList:[],
      wayList:[],
      relationId:'',
      wayId:''
    }
  },
  created() {
    this.relationList = {
      "list":[
        {"title": '爸爸','id':1},
        {"title": '妈妈','id':2},
        {"title": '爷爷','id':3},
        {"title": '奶奶','id':4},
        {"title": '其他','id':99},
      ],
      "num": 4
    };
    this.wayList = {
      "list":[
        {"content": '通过学生IC卡','id':1},
        {"content": '通过家长IC卡','id':2},
        {"content": '通过学生信息','id':3},
      ]
    };
  },
  methods: {
    getRelationList() {
      this.relationFlag = !this.relationFlag;
      this.relationId = 0;
    },
    getWayList() {
      this.bindWayFlag = !this.bindWayFlag;
      this.wayId = 0;
    },
    //选择关系
    chooseReaction(relationId, relationTitle) {
      this.getRelationList();
      this.relationId = relationId;
      this.relationTitle = relationTitle;
    },
    //选择绑定方式
    chooseBindWay(wayId, wayContent) {
      this.getWayList();
      this.wayId = wayId;
      this.wayContent = wayContent;
    },
    // 对绑定学生
    submit() {
      console.log("111");
      // this.$http.post().then((response) => {
      // });
    }

  },
  components: {
    'v-header': header, //注册头部组件
    'v-button': button //注册确定按钮
  },

}



